import React from 'react'
import waitress from '../picture/waitress.jpg'

var url = 'https://myanimelist.net/profile/Naxocist';

function Profile() {

	return (
		<div className="pf-wrap">

			<div className="profile">
				<a href={url} target="_blank" rel="noreferrer">
					<img className="profile-pic" src={waitress}
						alt="Naxocist's profile" />
				</a>
				<div className="info">
					<ul>
						<li>Naxocist</li>
						<li>Date of birth : 9<sup>th</sup> August 2006</li>
						<li>Favorite : Little Witch Academia</li>
					</ul>
				</div>
			</div>
		</div>
	)
}

export default Profile
